body {
  background-image: url(bg.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

@media only screen and (max-width: 767px) {
  body {
    background-image: url(bg.jpg);
  }
}
.intro {
  background: linear-gradient(110deg, #040652 60%, #104875 60%);
  background-size: cover;
  margin-top: 90px;
  border: 1px solid black;
  border-radius: 1vw;
  color: rgb(235, 221, 221);
  opacity: 0.8;
  max-height: 50vw;
}

.intro hr {
  border-top: 0.5vw solid rgb(132, 146, 207);
}

.hi {
  font-size: 3vw;
}
.name {
  text-align: center;
  font-size: 5vw;
}
.welcome {
  text-align: end;
  font-size: 4vw;
}

.skills {
  text-align: center;
  font-size: 2.5vw;
}

/* Icons begin */
.icons {
  display: flex;
  justify-content: space-evenly;
}
.icons .fa {
  font-size: 4vw;
  transition: 0.6s;
  color: rgb(228, 220, 220);
}
.icons .fa:hover {
  color: rgb(197, 185, 11);
  transform: translate(0, -10px);
}
/* Icons end */

.projects {
  margin-top: 10vw;
}

.footer {
  text-align: center;
  margin-top: 5vw;
  color: white;
  padding-top: 4em;
  padding-bottom: 1em;
  position: relative;
  bottom: 0;
  width: 100%;
}
.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page-wrap {
  flex: 1;
}
